import Loadable from 'react-loadable';

const Loading = () => null;
//按需加载
const LoadableComponent = loader => {
  return Loadable({
    loader,
    loading: Loading,
  })
}

const routes = [
  {
    path: '/home',
    component: LoadableComponent(() => import('@pages/home')),
    title: '机选双色球' // 当前路由的页面title
  },

  {
    path: '/404',
    component: LoadableComponent(() => import('@components/notFound'))
  }
]

export default routes