import { Route, Redirect, Switch } from "react-router-dom";
import routes from "../../router/routes";
const defaultTitle = '福彩'
const App = () => {
  return (
    <div className="App">
      <Switch>
        {routes.map((route) => {
          return (
            <Route
              key={route.path}
              path={route.path}
              render={props => {
                document.title = route.title || defaultTitle;
                return <route.component {...props}></route.component>
              }}
            />
          );
        })}
        <Redirect exact from="/" to={routes[0].path} />
        <Redirect to="/404" />
      </Switch>
    </div>
  );
};

export default App;
